@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap);
.rankUserItem{font-size:14px;color:#808080;cursor:pointer}.rankUserItem:hover{background-color:#fafafa}.rankUserItem p{padding:0;margin:0}.rankUserItem .rankUserItem-img img{width:80px;height:80px;border-radius:50%}.rankUserItem .rankUserItem-toatlContributions{font-weight:bold}.rankUserItem .rankUserItem-name{font-weight:bold}.rankUserItem .rankUserItem-name .rankUserItem-name-wrapper div{display:flex;align-items:center}.rankUserItem .rankUserItem-name .rankUserItem-name-wrapper div svg{padding-left:10px}.rankUserItem .rankUserItem-name .rankUserItem-name-wrapper span{font-weight:lighter;font-size:12px;color:#ff6f61}.rankUserItem .rankUserItem-bio,.rankUserItem .rankUserItem-userID{color:#a0a0a0}

.rankUser{font-family:'Noto Sans KR', sans-serif}.rankUser table{box-sizing:border-box;width:100%;margin:0 auto;padding:0;border-collapse:collapse}.rankUser table th,.rankUser table td{padding:10px 14px;border-bottom:1px solid #e2e2e2;text-align:start}.rankUser table th{font-size:14px;font-weight:bold;padding:30px 14px;color:#ff6f61}

.rankLoading{display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:18px}.rankLoading div{display:flex;align-items:center;justify-content:center}.rankLoading div p{padding:0 10px;margin:0}.rankLoading span{font-size:12px;margin-top:10px;color:#808080}

.title{background-color:#ff6f61;color:#ffffff;padding:50px 0;margin-bottom:50px;display:flex;justify-content:center}.title .title-content{display:flex;align-items:center;justify-content:flex-start;width:70%}@media only screen and (max-width: 1440px){.title .title-content{width:95%}}.title .title-content h2,.title .title-content p{margin:0;padding:0 10px}.title .title-content h2{font-size:36px;padding-left:0}.title .title-content p{font-size:14px}

.AddUserBox .IoMPersonAdd{cursor:pointer}

.InfoBox{text-align:end;width:30px}.InfoBox .AiFillInfoCircle{cursor:pointer}

.RankPage{min-width:1000px}.RankPage .contributionContainer{width:70%;margin:0 auto;margin-bottom:200px}.RankPage .contributionContainer .utilBox{display:flex;justify-content:flex-end}@media only screen and (max-width: 1440px){.RankPage .contributionContainer{width:95%}}

*{font-family:'Noto Sans KR', sans-serif}body{margin:0;padding:0}

